.audio-player-component {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 30px;
  width: 600px;
  height: 56px;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.1);
  padding-left: 24px;
  padding-right: 24px;
  border-radius: 4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #fff;
}
.audio-player-component--slider {
  width: 300px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  background-color: #D8D8D8;
  cursor: pointer;
  position: relative;
  margin-left: 16px;
  margin-right: 16px;
  border-radius: 1.5px;
  border-top: 16px solid white;
  border-bottom: 16px solid white;
  height: 3px;
}
.audio-player-component--bar-0 {
  background-color: #3374EB;
  border-radius: 1.5px;
  height: 3px;
}
.audio-player-component--handle {
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: #3374EB;
  position: absolute;
  top: -4.5px;
  pointer-events: all;
  box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.32);
}
.audio-player-component--time {
  font-size: 14px;
  color: #8494a6;
}
.audio-player-component--btn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border: none;
  outline: none;
  background-color: initial;
}
.audio-player-component--icon {
  cursor: pointer;
  width: 36px;
  height: 36px;
  fill: #3374EB;
}
