.dialogue-component {
  background-color: white;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.1);
  width: 806px;
  border-radius: 4px;
  padding: 20px 30px;
  -ms-flex: 1;
      flex: 1;
  margin-top: 50px;
}
.dialogue-component--header {
  border-bottom: 1px solid #ececec;
  margin-bottom: 30px;
}
.dialogue-component--title {
  margin: 10px 0 15px;
}
.dialogue-component--time {
  color: #b3b3b3;
  margin: 0 0 10px;
}
.dialogue-component--container {
  overflow-y: auto;
}
